<template>
  <div id="wangeditor">
    <div ref="editorElem" style="text-align:left;"></div>
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  name: 'WangEditor',
  data () {
    return {
      editor: null,
      editorContent: ''
    }
  },
  // catchDatahtml
  props: ['catchData'], // 
  mounted () {
    this.editor = new E(this.$refs.editorElem)
    // html
    this.editor.customConfig.onchange = html => {
      this.editorContent = html
      this.catchData(this.editorContent) // htmlcatchData
    }
    this.editor.customConfig.menus = [
      // 
      'head', // 
      'bold', // 
      'fontSize', // 
      'fontName', // 
      'italic', // 
      'underline', // 
      'strikeThrough', // 
      'foreColor', // 
      'backColor', // 
      'link', // 
      'list', // 
      'justify', // 
      'quote', // 
      'emoticon', // 
      'image', // 
      'table', // 
      'code', // 
      'undo', // 
      'redo' // 
    ]
    this.editor.create()
  }
}
</script>

<style lang="less">
  .w-e-text-container{
    height: calc(85vh - 101.2px) !important;
  }
  @media screen and (max-width: 1300px){
    .w-e-text-container {
      height: calc(100vh - 101.2px) !important;
    }
  }
</style>
